<template>
    <section>
        <tg-list :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="FSM_WorkOrder"
            :LegendArray="[]" :widgets="[]" IdentityColumn="id" :listType="['List']" :HeaderText="$t('Wokr Order')"
            :ListData="WorkOrderList" :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"  
            :SortExp="SortExp" :SortBy="SortBy"  :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" 
            :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'status_id'">
                    <td class="text-center single-action tourguide_a">
                        <span v-bind:class="getStatusClass(data.row.status_id, 'status_id')">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                    </td>

                </template>                
            
            </template>                         
        </tg-list>
           
    </section>
</template>

<script>
import Schema from "@/views/WorkOrder/WorkOrderSchema";
import DataServices from "@/services/DataService";
export default {
    components: {

    },
    props:{
        workorderlist :{
            type: Array,
            required: false
        }
    },
    watch: {
        workorderlist: function (value) {
            debugger;
            this.WorkOrderList = value;
            this.IsListView = this.$parent.IsListView;      
        },
    },
    //  created: async function () {
    //     debugger;
    //     var vm = this;
    //     vm.Workorderlist = vm.workorderlist;
    //     vm.IsListView = vm.$parent.IsListView;  
    // },
    data() {
        return {
            WorkOrderList:[],
            TotalRecords: 0,
            PageSize: 10,
            PageNumber: 1,
            CurrentPage: 1,
            SortBy: '',
            SortExp: '',
            IsInSlot:true,
            listheaderbuttons: null,
            listActions: Schema.workOrderlistActions,
            Headers: [{
                "COLUMN_NAME": "workorder_number",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "WO NUMBER",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "status_id",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "worktype_name",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "WO Name",
                "DISPLAY_ORDER ": 3,
            },
            {

                "COLUMN_NAME": "work_order_date",
                "DATA_TYPE": "date",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Work Order Date",
                "DISPLAY_ORDER": 4,

            },
            {
                "COLUMN_NAME": "comment",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Comment",
                "DISPLAY_ORDER": 5,
            },

            {
                "COLUMN_NAME": "zone_name",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "High Profile Zone Name",
                "DISPLAY_ORDER": 6,
            },

            {
                "COLUMN_NAME": "is_submited",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Form Submitted",
                "DISPLAY_ORDER": 7,
            },
            ],
        }
    },  
    updated: function () {
        debugger;
        //this.BindActionButtonEvent();
        this.SetScrolbar('dark-thick', '', 'filterscroll_leftsearch');
    }, 
    methods: {
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            //this.FetchRecords();
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            debugger;
            switch (callbackfunction) {
                case 'Edit':
                    break;
                case 'OpenFileModal':
                    this.OpenFileModal(item);
                    break;
                case 'ManageFollowUp':
                    this.ManageFollowUp(item.ticket_id, item);
                    break;
                case 'openQuickViewpopup':
                    this.openQuickViewpopup(item.ticket_id, item.str_ticket_id, item.ticket_category_id);
                    break;


            }
        },
        getStatusClass: function (status) {
            debugger;
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                case 'Open':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        renderActions: function (row) {
            debugger;
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Detail':
                        action.isVisible = self.IsInSlot == true && (row.TICKET_AR_TYPE == '' && row.IS_SHOW_ACCEPT_REJECT == 0);
                        break;
                    case 'Update Status':
                        action.isVisible = self.IsInSlot == true && (row.TICKET_AR_TYPE == '' && row.IS_SHOW_ACCEPT_REJECT == 0);
                        break;                    
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
    }
}
</script>